import Swiper, { Pagination, Navigation, EffectFade, Thumbs } from 'swiper';
import MicroModal from 'micromodal';

document.addEventListener('DOMContentLoaded', () => {


    function modTwoBlocks() {
        MicroModal.init();
    
        const smiSlider = new Swiper('.smi__slider', {
            modules: [Pagination],
            pagination: {
                el: '.smi__pag',
                type: 'bullets',
                bulletClass: 'smi__pag_item',
                bulletActiveClass: 'smi__pag_item_active',
                bulletElement: 'span',
                clickable: true,
            },
            breakpoints: {
                1200: {
                    slidesPerView: 7,
                    spaceBetween: 44
                },
                768: {
                    slidesPerView: 5,
                    spaceBetween: 44                
                },
                320: {
                    slidesPerView: 3,
                    spaceBetween: 20,
                }
            },
        });


        const popSlider = new Swiper('.pop__slider', {
            modules: [Navigation],
            navigation: {
                nextEl: '.pop__next',
                prevEl: '.pop__prev'
            },
            breakpoints: {
                1200: {
                    slidesPerView: 3,
                    spaceBetween: 28
                },
                768: {
                    slidesPerView: 3,
                    spaceBetween: 18
                },
                320: {
                    slidesPerView: 1,
                    spaceBetween: 20
                }
            }
        });

        const swiperPrev = document.getElementById('sw-prev');
        const swiperNext = document.getElementById('sw-next');

        if(swiperPrev) {
            swiperPrev.addEventListener('click', () => {
                popSlider.slidePrev();
            });
        }
        if(swiperNext) {
            swiperNext.addEventListener('click', () => {
                popSlider.slideNext();
            });
        }


        const popModalSlider = new Swiper('.modal__slider', {
            modules: [EffectFade, Thumbs, Navigation],
            slidesPerView: 1,
            effect: 'fade',
            thumbs: {
                swiper: popSlider
            },
            navigation: {
                nextEl: '.modal__next',
                prevEl: '.modal__prev'
            }
        });
    }

    modTwoBlocks();

    
});





